  <div id="home">
    <banner></banner>
    <div class="wrapper">
      <div class="cards-wrapper">
        <card v-bind:header="groupIntroduction.header"
            v-bind:text="groupIntroduction.text"
            grid="grid-2"
            v-bind:button="groupIntroduction.button.text"
            v-bind:link="groupIntroduction.button.link">
        </card>
      </div>
      <div class="cards-wrapper">
