            text: 'Explore more',
            link: '/#!/about'
          }
        },
        news: {
          header: 'News',
          button: {
            text: 'More',
            link: '/'
          }
        },
        chunhuiInstitute: {
          header: 'Chunhui Institute',
          button: {
            text: 'More',
            link: '/'
          }
        }
